@font-face {
  font-family: "Enixe";
  src: local("Enixe"), url("./../fonts/Enixe.ttf") format("truetype");
  font-weight: bold;
}

:root {
  --swipe-width: 80px;
}

html {
  height: 100%;
}
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.root {
  position: absolute;
  width: 100%;
  height: 100%;
  /* background: url('./../imgs/space-bg.jpg'); */
  background-color: #000;
}

.swipe {
  position: absolute;
  top: 50%;
  /* left: 50%; */
  transform: translate(-50%, -50%);
  width: var(--swipe-width);
  height: 160px;
  font-size: 28px;
  font-family: tahoma;
  color: #fff;
  background: #00bcd418;
  transition: all 0.3s ease;
  cursor: pointer;
  z-index: 5;
  border: none;
}

.swipe:hover {
  background: #00bcd499;
}

.left {
  left: calc(100% - var(--swipe-width) / 2);
}

.right {
  left: calc(0% + var(--swipe-width) / 2);
}

.hide {
  display: none;
}
